  /* styles.css */

  /* Add global styles here */

  body {
    /* font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      min-height: 100vh; /* Make sure the body takes up at least the full height of the viewport */
    }
    
    header {
      
      /* font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; */
      background-color:brown;
      color: rgb(115, 115, 254);
      padding: 10px;
      /* text-align: center;   */
      display: flex;
      justify-content: space-between;
      align-items: center;

    }
    
    
    .header p {
      margin-left: auto; /* This will push the paragraph to the right */
    }

  
    .header-btn{
      color: #ffffff;
    }
    
    header h1 {
      margin: 0;
    }
    
    
    nav {
      background-color: rgb(0, 255, 26);
      padding: 10px;
    }
    
    .tabs {
      display: flex;
      /* justify-content: space-around; */
      /* Smooth transition for box-shadow */
    

    }
    
    .tab {
      background-color: #ffffff;
      color: #2200ff;
      border: none;
      padding: 10px 20px;
      border-radius: 5px;
      margin-left: 15px;
      /* transition: transform 0.25s ease-in-out; */
    }
    
    .tab:hover {
      /* background-color: #bbb; */
      box-shadow: 0 5px 10px rgba(187, 0, 0, 0.584); 
    }
    
    main {
      padding: 20px;
      flex-grow: 1; /* Allow main content to grow to fill remaining space */
    }
    
    footer {
      background-color: #ffffff;
      color: #fff;
      padding: 10px;
      text-align: center;
      margin-top: 500px; /* Push the footer to the bottom of the page */
    }
    